import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd, NavigationStart, RoutesRecognized } from "@angular/router"
import { DomSanitizer } from '@angular/platform-browser';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph"
import { Subscription } from 'rxjs/Subscription';

@Component({
  templateUrl: './students.cmp.html',
  styleUrls: ['./students.cmp.scss']
})
export class StudentsCmp implements OnInit, OnDestroy {
  tempUrl :string = 'assets/libs/download.jpeg';
  students: any[] = [];
  subscription_get_students: Subscription;
  constructor(private _router: Router, private _dataRouting: DataRouting, private _broadcaster: Broadcaster, private _DomSanitizationService: DomSanitizer) {
  }
  ngOnInit() {
    this.registerEvents();
  }
  ngOnDestroy() {
    this.unRegisterBroadcast();
  }
  registerEvents() {
    this.subscription_get_students = this._broadcaster.on<string>('get_students')
      .subscribe((result: any) => {
        this.students = result;
      })
  }
  unRegisterBroadcast() {
    this.subscription_get_students.unsubscribe();
  }
  gotoUser(id) {
    this._router.navigate(['/', id]);
  }
}


