import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { FileUploader } from 'ng2-file-upload';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph";
@Component({
    templateUrl: './student.detail.cmp.html',
    styleUrls: ['./student.detail.cmp.scss']
})
export class StudentDetailCmp implements OnInit, OnDestroy {

    goals = [
        { id: 'weightLoss', test: 'Weight Loss' },
        { id: 'weightGain', test: 'Weight Gain' }
    ];

    form: FormGroup;
    instructors = [];
    studentData: any = {};
    public uploader: FileUploader = new FileUploader({});
    private success: string = "";
    subscription_register_instructors: Subscription;
    subscription_get_student_data: Subscription;

    constructor(private _fb: FormBuilder, private _route: ActivatedRoute, private _broadcaster: Broadcaster, private _DomSanitizationService: DomSanitizer, private _dataRouting: DataRouting) {
    }

    ngOnInit() {

        this.uploader.onAfterAddingFile = this.selectPhoto.bind(this);
        let id = (this._route.snapshot.params['id']);
        if (id) {
            // this.selectedUser = this._dataRouting.getUser(id);
        }
        else {
            this.studentData.url = 'assets/libs/download.jpeg';
        }
        this.registerEvents();

        this.form = this._fb.group({
            instructor: ['', Validators.required],
            age: ['', Validators.required],
            goal: ['', Validators.required],
            height: ['', Validators.required],
            weight: ['', Validators.required]
        })
        this.instructors = this._dataRouting.instructorsTest;
    }

    ngOnDestroy() {
        this.unRegisterEvents();
    }

    registerEvents() {
        this.subscription_register_instructors = this._broadcaster.on<string>('instructorReady')
            .subscribe((photoObj: any) => {
                // this.instructors = this._dataRouting.instructors;
                // this.instructors.map((ins)=>{
                //    this.instructorsTest.push({givenName:ins.givenName,displayName:ins.displayName})     
                // })

            })
        this.subscription_get_student_data = this._broadcaster.on<string>('get_student_data')
            .subscribe((result: any) => {
                this.studentData = this._dataRouting.currentStudent;
                if (this.studentData) {
                    this.form = this._fb.group({
                        instructor: [this.studentData[1], Validators.required],
                        age: [this.studentData[2], Validators.required],
                        goal: [this.studentData[3], Validators.required],
                        height: [this.studentData[4], Validators.required],
                        weight: [this.studentData[5], Validators.required]
                    })
                }
                else {
                    this.form = this._fb.group({
                        instructor: ['', Validators.required],
                        age: ['', Validators.required],
                        goal: ['', Validators.required],
                        height: ['', Validators.required],
                        weight: ['', Validators.required]
                    })
                }
            })
    }

    unRegisterEvents() {
        this.subscription_register_instructors.unsubscribe();
        this.subscription_get_student_data.unsubscribe();
    }

    selectPhoto(fileItem: any) {

        let that = this;
        var reader = new FileReader();
        reader.addEventListener("load", (e: any) => {
            this.studentData.url = e.target.result
        }, false);
        if (fileItem && fileItem.some.type.match('image.*')) {
            reader.readAsDataURL(fileItem.some);
        }
    }

    submit() {
        let excelUrl = ""
        let data = {};
        let data1 = {};
        // let file: any = this.uploader.queue[0];
        // if (file && file.some) {
        //     let url = "me/photo/$value";
        //    // this._dataRouting.put("photo", url, file.some);
        // }
        if (this.studentData) {
            //Add values to studentData.
            let row_index = +this.studentData[6] - 2;
            excelUrl = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/rows/itemAt(index=' + row_index + ')';
            data = { "values": [[this._dataRouting.currentUser.userPrincipalName, this.form.value.instructor, this.form.value.age, this.form.value.goal, this.form.value.height, this.form.value.weight, '=Row()']] }
            data1 = { "values": [[this._dataRouting.currentUser.userPrincipalName, this.form.value.instructor, this.form.value.age, this.form.value.goal, this.form.value.height, this.form.value.weight, this.studentData[6]]] }
            this._dataRouting.uiEvents("student_update", excelUrl, data, data1);
        }
        else {
            excelUrl = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/rows/add";
            data = {
                "values": [[this._dataRouting.currentUser.userPrincipalName, this.form.value.instructor, this.form.value.age, this.form.value.goal, this.form.value.height, this.form.value.weight, '=Row()']]
            }
            this._dataRouting.uiEvents("student_add", excelUrl, data, data1);
            // this._dataRouting.post("students", excelUrl, data, this._dataRouting.persistsId);
        }
    }
}




